import React, { Component } from "react";
import "./styles.css";
import Logo from "../../assets/images/iconnew.png";
import axios from "axios";

export default class Form extends Component {
  state = {
    token: "",
    question1: "",
    question2: "",
    question3: "",
    response: false,
    loading: false,
    responseText: null,
    success: false,
    noToken: false,
  };
  componentDidMount() {
    const { token } = this.props.match.params;
    if (token.length < 25) {
      this.setState({ loading: true, noToken: true });
    } else {
      this.setState({ token });
    }
  }
  sendForm = async (e) => {
    e.preventDefault();
    this.setState({ loading: true });
    const { question1, question2, question3, token } = this.state;
    const api = axios.create({
      baseURL: "https://safegirl.herokuapp.com",
      headers: { Authorization: `Bearer ${token}` },
    });
    if (
      question1.length > 10 &&
      question2.length > 10 &&
      parseInt(question3) > 0
    ) {
      console.log({ question1, question2, question3 });
      try {
        await api.post("/evaluate-beta", { question1, question2, question3 });
        this.setState({
          success: true,
          response: true,
          loading: false,
          question1: "",
          question2: "",
          question3: "",
        });
      } catch (err) {
        console.log(err);
        this.setState({
          response: true,
          responseText:
            "Ocorreu um erro ao salvar seu formulário, tente novamente",
          loading: false,
        });
      }
    } else {
      this.setState({
        response: true,
        responseText: "Preencha os campos de texto com no mínimo 10 caracteres",
        loading: false,
      });
    }
  };
  onRadioChange = (e) => {
    this.setState({ question3: parseInt(e.currentTarget.value) });
  };
  render() {
    const {
      loading,
      question1,
      question2,
      question3,
      response,
      success,
      responseText,
      noToken,
    } = this.state;
    return (
      <div className="container">
        {!loading ? (
          <div className="form">
            <div className="form-header">
              <img src={Logo} alt="Logo" />
              <h1>Formulário de avaliação</h1>
            </div>
            {response && (
              <div
                className={success ? `alert alert-sucess` : "alert alert-error"}
              >
                <p>
                  {success ? "Obrigado pela sua avaliação :)" : responseText}
                </p>
                <button onClick={() => this.setState({ response: false })}>
                  x
                </button>
              </div>
            )}
            <div className="form-group">
              <p>
                O que você achou do aplicativo? Ele será útil para você? Foi
                fácil usar as funcionalidades?
              </p>
              <textarea
                value={question1}
                onChange={(e) => this.setState({ question1: e.target.value })}
                rows="5"
              />
            </div>
            <div className="form-group">
              <p>Quais sugestões você possui para melhorar a plataforma? </p>
              <textarea
                value={question2}
                onChange={(e) => this.setState({ question2: e.target.value })}
                rows="5"
              />
            </div>
            <div className="form-group">
              <p>De 1 a 5, qual a nota você dá para o aplicativo?</p>
              <label> 1 </label>
              <label> 2 </label>
              <label> 3 </label>
              <label> 4 </label>
              <label> 5 </label>
              <br />
              <input
                onChange={this.onRadioChange}
                checked={question3 === 1}
                type="radio"
                value="1"
              />
              <input
                onChange={this.onRadioChange}
                checked={question3 === 2}
                type="radio"
                value="2"
              />
              <input
                onChange={this.onRadioChange}
                checked={question3 === 3}
                type="radio"
                value="3"
              />
              <input
                onChange={this.onRadioChange}
                checked={question3 === 4}
                type="radio"
                value="4"
              />
              <input
                onChange={this.onRadioChange}
                checked={question3 === 5}
                type="radio"
                value="5"
              />
            </div>
            <div className="form-footer">
              <a href="/" onClick={this.sendForm}>
                Enviar Avaliação
              </a>
            </div>
          </div>
        ) : (
          <div className="loading">
            {!noToken ? (
              <div className="loading-container">
                <h1>Processando...</h1>
                <img
                  src="https://loading.io/spinners/eclipse/lg.ring-loading-gif.gif"
                  alt="loading"
                />
              </div>
            ) : (
              <h1>Você não tem permissão para avaliar :(</h1>
            )}
          </div>
        )}
      </div>
    );
  }
}
